import React from "react";
import moment from "moment";

const About = () => {
  //This function traduze for portuguese
  const langMoment = () => {
    const config = moment.updateLocale("en", {
      relativeTime: {
        future: "in %s",
        past: "%s ",
        s: "a few seconds",
        ss: "%d segundos",
        m: "um minuto",
        mm: "%d minutos",
        h: "um hora",
        hh: "%d horas",
        d: "um dia",
        dd: "%d dias",
        M: "um mês",
        MM: "%d meses",
        y: "um ano",
        yy: "%d anos"
      }
    });

    return config;
  };

  // This function return numbers of years
  const calculatorDate = date => {
    langMoment();
    const today = moment(date, "YYYYMMDD").fromNow();
    return today;
  };

  return (
    <section className="card-page">
      <article>
        <div>
          <header>
            <h2 className="title-page">Sobre</h2>
          </header>
          <p>
            Eu tenho {calculatorDate(19900130)} de idade, moro no Rio de
            Janeiro, sou <b>desenvolvedor full stack</b>, e há mais de {""}
            {calculatorDate(20130910)} <br />
            atuo com desenvolvimento de diversas e variadas aplicações
            participando estrategicamente de quase
            <br /> todas as etapas, indo da{" "}
            <b> concepção à homologação. {""} </b> Sempre buscando {""}
            <b>produtividade e alta performance</b>
            <br /> no desenvolvimento {""}
            <b>web ou mobile</b> para melhor atender às variadas demandas do
            mercado.
          </p>
        </div>
      </article>
    </section>
  );
};
export default About;
